/** VARIABLES */
:root {
  --text-color: #000;
  --text-color-light: #fff;

  --brand-green: #b9ff66;
  --brand-dark: #191a23;
}

/** TYPOGRAPHY */
h1,
h2,
h3,
h4 {
  font-weight: 500;
  color: var(--text-color);
  width: fit-content;
}

.heading-bg {
  padding: 0 0.438rem;
  border-radius: 7px;
}

.heading-bg.brand-green {
  background-color: var(--brand-green);
}

.heading-bg.white {
  background-color: #fff;
}

h1 {
  font-size: 3.75rem;
}

h2 {
  font-size: 2.5rem;
}

h3 {
  font-size: 1.875rem;
}

h4 {
  font-size: 1.25rem;
}

p {
  font-size: 1.125rem;
}

a {
  text-decoration: none;
  color: inherit;
  display: flex;
}

ul {
  list-style: none;
}

button {
  cursor: pointer;
}

/** GLOBAL CLASSES AND STYLES */
.section-content-container {
  display: flex;
  justify-content: center;
  scroll-margin-top: 0;
}

.section-content {
  width: 90vw;
  max-width: 1240px;
}

.common-shadow {
  border: 1px solid var(--brand-dark);
  box-shadow: 0 5px 0 var(--brand-dark);
}

.common-border-radius {
  border-radius: 45px;
}

@media (max-width: 1228px) {
  html {
    font-size: 14px;
  }

  p {
    font-size: 1.25rem;
  }
}

@media (max-width: 1070px) {
  html {
    font-size: 13px;
  }

  p {
    font-size: 1.45rem;
  }

  h4 {
    font-size: 1.45rem;
  }
}

@media (max-width: 490px) {
  html {
    font-size: 10px;
  }

  p {
    font-size: 2rem;
  }
}
